import React from 'react';
import './App.css';


class App extends React.Component{
	constructor(props) {
    	super(props);
    	this.clickhandler = this.clickhandler.bind(this);
		this.state = {
			background: ['#e0635a','#d19a66','#f7d572','#98c379','#367dda','#b9679e'],
			currentbg : 6,
		}
  	}
  	componentDidMount() {
  		var IntervalID = setInterval(this.clickhandler,1000);
  		this.setState({
  			IntervalID : IntervalID
  		})
  	}
  	componentWillUnmount() {
  		clearInterval(this.state.IntervalID)
  	}
	clickhandler() {
		this.setState({
			currentbg : this.state.currentbg + 1
		})
	}

	render(){
		var bgstyle = {
			background: this.state.background[this.state.currentbg%6]
		}
		return(
			<div className="bg" style={bgstyle}>
				<button onClick={this.clickhandler}>Change</button>
			</div>
		)
	}

}

export default App;
